.styleguide {
  padding: 10px 20px;
  background-color: #303030;
  color: white; }

.styleguide-name {
  text-align: center; }

h3 {
  padding: 11px 20px;
  margin: 0;
  color: #f2ed83; }

.links,
.buttons,
.inputs,
.typography-elements,
.colors,
.icons {
  background-color: #444;
  padding: 13px;
  border-radius: 5px;
  margin: 10px 0; }

.link {
  margin-left: 20px;
  font-size: 20px; }

.input {
  padding-left: 20px;
  color: white; }

.MuiInput-input {
  color: red;
  background: red; }

.color-styleguide {
  color: white;
  margin: 5px 10px 5px 20px;
  display: inline-block;
  padding: 40px;
  font-size: 40px;
  width: 162px;
  text-align: center; }

.first,
.second {
  border-radius: 5px;
  border: solid 1px; }

.first {
  background-color: #444; }

.second {
  background-color: #008966; }

.MuiButton-containedSecondary {
  background-color: #008966 !important; }
